import { createSelector } from 'reselect'

export const getTrips = state => state.trips.trips
export const getTrip = state => state.trips.trip
export const getPeriods = state => state.trips.period
export const getRoute = state => state.trips.route
export const getTariff = state => state.trips.tariff
export const currentStop = state => state.trips.currentStop
export const getTypeTrip = state => state.trips.typeTrip
export const getLoader = state => state.trips.loader
export const getError = state => state.trips.error

export const getPeriodTimes = createSelector(getPeriods, periods => {
  if (periods) {
    return periods.find(el => el.hasOwnProperty('times'))
  }
})

export const getPeriodDays = createSelector(getPeriods, periods => {
  if (periods) {
    return periods.find(el => el.hasOwnProperty('days'))
  }
})

export const getCurrentStop = createSelector(getRoute, currentStop, (route, stop) => {
  return stop ? stop : route[0]?.locality.parent.id
})

export const getTariffsArray = createSelector(getRoute, getTariff, (route, tariffs) => {
  if (route.length > 0) {
    return route.reduce(
      (arr, el) => {
        const fromArray = route.reduce((acc, item, ind) => {
          if (el.locality.parent.id !== item.locality.parent.id) {
            if (
              !tariffs.some(
                i =>
                  i.fromId === el.locality.parent.id &&
                  i.toId === route[ind].locality.parent.id
              )
            )
              if (
                route.findIndex(i => i.locality.parent.id === el.locality.parent.id) <=
                ind
              )
                acc.push({
                  fromId: el.locality.parent.id,
                  toId: route[ind].locality.parent.id,
                  value: '',
                })
          }
          return acc
        }, [])
        arr.push(...fromArray)
        return arr
      },
      [...tariffs]
    )
  }
  return []
})
