import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import TextInput from '../components/FormikInputs/TextInput'
import Loading from '../components/Loader'
import { ButtonRound, ErrorText } from '../stylesheets/mainStyled'
import { ReactComponent as NextIcon } from '../images/next.svg'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import { addTrip, editTrip } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'
import TripHeader from '../components/TripsContainer/TripHeader'
import { tripId, tripsError, tripsLoader } from '../redux/trips/tripsActions'
import { getError, getLoader, getTrip, getTypeTrip } from '../redux/trips/tripsSelectors'

const TripMain = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  const auth = useSelector(getAuth)
  const trip = useSelector(getTrip)
  const error = useSelector(getError)
  const loader = useSelector(getLoader)
  const typeTrip = useSelector(getTypeTrip)

  return (
    <>
      <TripHeader />
      <Container>
        <Title>
          Название и комментарий будут видны покупателям. Так пассажиры поймут детали
          вашего рейса.
        </Title>
        <Formik
          initialValues={
            trip
              ? { ...trip }
              : {
                  name: '',
                  placeQuota: '',
                  comment: '',
                }
          }
          validationSchema={Yup.object({
            name: Yup.string().required('is required'),
            placeQuota: Yup.number().required('is required'),
            // comment: Yup.string().required('is required'),
          })}
          onSubmit={async ({ name, placeQuota, comment }) => {
            const tripData = trip
              ? { ...trip, name, placeQuota, comment }
              : { name, placeQuota, comment }
            dispatch(tripsLoader())
            try {
              if (typeTrip === 'edit') {
                const { data } = await editTrip(tripData, auth)
                history.push({ pathname: `/trip-edit`, search: `?tripId=${data.id}` })
              }
              if (typeTrip === 'create') {
                const { data } = await addTrip(tripData, auth)
                dispatch(tripId(data))
                history.push({
                  pathname: `/trip-periods`,
                  search: `?tripId=${data.id}`,
                  state: { from: 'trip-main' },
                })
              }
            } catch (error) {
              dispatch(tripsError(error))
            }
          }}
        >
          <Form>
            <TextInput label="Название" name="name" type="text" />
            <TextInput label="Лимит мест" name="placeQuota" type="text" />
            <TextInput label="Комментарий" name="comment" type="text" />
            {error && <ErrorText>{error.message}</ErrorText>}
            <ButtonRound type="submit">
              {loader ? (
                <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
              ) : typeTrip === 'edit' ? (
                <CheckIcon />
              ) : (
                <NextIcon />
              )}
            </ButtonRound>
          </Form>
        </Formik>
      </Container>
    </>
  )
}

export default TripMain

const Container = styled.div`
  padding: 10px 20px;
`
const Title = styled.h2`
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 20px;
`
