import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { ReactComponent as CloseIcon } from '../../images/close.svg'
import { ReactComponent as ArrowBackIcon } from '../../images/arrowBack.svg'

const TripHeader = ({ onGoBack, name = null }) => {
  return (
    <Header>
      <div>
        {onGoBack && <ArrowBackIcon onClick={onGoBack} style={{ cursor: 'pointer' }} />}
        <Title>Рейс {name}</Title>
      </div>
      <Link to="/trips">
        <CloseIcon />
      </Link>
    </Header>
  )
}

export default TripHeader

const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
  padding: 12.4px 24px;
  & div {
    display: flex;
    align-items: center;
  }
`

const Title = styled.p`
  font-size: 24px;
  padding-left: 12px;
`
